import request from '@/utils/request'
export function findCommentsByTarget(params) {
  return request({
    url:'/comment/findCommentsByTarget',
    method:'get',
    params:params
  })
}

export function addComment(params) {
  return request({
    url:'/comment/addComment',
    method:'post',
    params:params
  })
}

export function delComment(params) {
  return request({
    url:'/comment/delComment',
    method:'post',
    params:params
  })
}



