<template>
  <div>
    <!-- 阅读评论弹窗 -->
    <div>
      <el-dialog :visible.sync="show.readComment" width="90%">
        <div v-html="v.ed.content"></div>
        <el-divider></el-divider>
        <el-button>就你了</el-button>
        <el-button @click="show.readComment= !1">当作没看见</el-button>
      </el-dialog>
    </div>

    <!-- 添加评论弹窗 -->
    <el-dialog :visible.sync="show.addComment" width="90%" :title="v.commentType==1?'吐槽':'回复悬赏'">
      <!-- 快速吐槽用输入框 -->
      <el-input v-if="v.commentType==1" v-model.trim="v.addCommentContent"></el-input>
      <!-- 提交悬赏/征集用富文本 -->
      <el-form v-if="v.commentType==2 || v.commentType==3">
        <el-form-item label="标题">
          <el-input v-model.trim="v.addCommentTitle"></el-input>
        </el-form-item>
        <el-form-item label="回复内容">
          <div>
            <!-- 单独加v-if的原因是重新初始化编辑器，让其能清空内容 -->
            <tinymce v-model="v.addCommentContent" v-if="show.addComment"></tinymce>
          </div>
        </el-form-item>

        <el-form-item label="添加附件" v-if="show.upload">
          <!-- 多家一个v-if是为了清空数据 -->
          <el-upload :action="dataUploadPath" :on-success="fileUploadSuccess" :limit="1" v-if="show.addComment">
            <el-button>上传数据</el-button>
          </el-upload>
        </el-form-item>
      </el-form>
      <el-divider></el-divider>
      <el-button @click="addComment('commit')">发射</el-button>
      <el-button @click="addComment('cancel')">当我没说</el-button>
    </el-dialog>

    <!-- 头部 -->
    <flat-grey-head></flat-grey-head>

    <blogHeader :blogObj="blogObj" v-if="v.type=='blog'"></blogHeader>
    <recruitHeader v-if="v.type=='recruit'"></recruitHeader>

    <div style="padding-left:100px; padding-right:100px;">
      <div style=" padding-bottom: 100px">
        <h2 v-if="v.type=='recruit'">岗位详情</h2>
        <h2 v-if="v.type=='blog'">帖子</h2>
        <h2 v-if="v.type=='solution'">Solution</h2>

        <!-- 招聘信息多一个分割线和链接地址 -->
        <el-divider v-if="v.type=='recruit'"></el-divider>

        信息来源：
        <el-tag v-if="v.type=='recruit'">
          <a :href="v.linkAddress" target="_blank">{{detailObj.informationFrom}}</a>
        </el-tag>

        <el-divider></el-divider>
       
       <!-- 招聘信息具体内容用表单显示 -->
       <el-form v-if="v.type=='recruit'">
         <el-form-item label="岗位标题：">
           <span>{{detailObj.title}}</span>
         </el-form-item>
         <el-form-item label="公司：">
           <span>{{detailObj.company.companyName}}</span>
         </el-form-item>
         <el-form-item label="工作地点：">
           <span>{{detailObj.location}}</span>
         </el-form-item>
         <el-form-item label="薪资：">
           <span>{{detailObj.salaryMonthMin}}-{{detailObj.salaryMonthMax}}</span>
         </el-form-item>
         <el-form-item label="有效期：">
           <span>{{detailObj.beginTime}}~{{detailObj.endTime}}</span>
         </el-form-item>
         <el-form-item label="岗位详情：">
           <div v-html="detailObj.content"></div>
           
         </el-form-item>
         
       </el-form>

        <!-- 如果是blog 和 solution-->
        <div v-if="v.type=='blog' || v.type=='solution'">
          <div v-html="decodeURIComponent(blogObj.content)"></div>
        </div>
      </div>

      <el-divider v-if="v.type=='blog' || v.type=='solution'">华丽的分割线</el-divider>
      <el-divider v-if="v.type=='recruit'">到底了</el-divider>

      <!-- 评论  (招聘信息没有评论)-->
      <div v-if="v.type=='blog' || v.type=='solution'">
        <h2>评论</h2>
        <el-row>
          <el-col :span="10"></el-col>
          <el-col :span="4">
            <el-button @click="addCommentReset(),v.commentType=1, addComment('show')">快速吐槽</el-button>
          </el-col>

          <el-col :span="4">
            <el-button @click="addCommentReset(),v.commentType=2 ,addComment('want')">强力吐槽</el-button>
          </el-col>
        </el-row>
        <div>
          <el-table :data="list.comments" style="width: 100%">
            <!-- 头像列 -->
            <el-table-column width="200">
              <template slot-scope="scope">
                <img :src="scope.row.creatorLogo" style="width: 50px; height: 50px">
              </template>
            </el-table-column>

            <el-table-column width="50">
              <template slot-scope="scope">
                <!-- 如果有附件 -->
                <img src="../assets/images/default-rewant-logo.png" style="width: 50px; height: 50px" v-if="scope.row.attachmentFileCode">
              </template>
            </el-table-column>

            <!-- 评论内容 -->
            <el-table-column>
              <template slot-scope="scope">
                <!-- 吐槽直接显示 (简易吐槽没有标题)-->
                <div v-if="!scope.row.title" @click="v.ed=scope.row, show.readComment=!0" style="cursor: pointer">{{scope.row.content}}</div>

                <!-- 征集和悬赏用显示标题 -->
                <div v-else @click="v.ed=scope.row, show.readComment=!0" style="cursor: pointer">{{scope.row.title}}</div>
              </template>
            </el-table-column>
          </el-table>

          <!-- 评论分页 -->
          <div>
            <el-pagination @current-change="changePange" :total="v.count" :page-size="v.pageSize" :current-page="v.pageIndex"></el-pagination>
          </div>
        </div>
      </div>
    </div>
    <!-- 内容 -->
  </div>
</template>
<script>
import flatGreyHead from "@/components/header/flatGreyHead";
import recruitHeader from "@/components/header/recruitHeader";

import blogHeader from "@/components/header/blogHeader";
import tinymce from "@/components/Tinymce/index";
import { detail as r_detail } from "@/api/recruit.js";
import { getBlog, blogComments, addBlogComment } from "@/api/blog.js";
import { getSolution } from "@/api/solution.js";

import { addComment, findCommentsByTarget } from "@/api/comments.js";

import qs from "qs";

export default {
  components: {
    flatGreyHead,
    tinymce,
    blogHeader,
    recruitHeader
  },

  props: {
    type: {
      default: "",
      type: String
    }
  },
  beforeMount() {
    //   窗口宽度
    let width = document.body.clientWidth;

    // 乘以0.9是弹窗的宽度
    width *= 0.9;

    // 再乘以0.9是label的宽度
    width *= 0.81;

    this.v.tinymceWidth = width;
  },
  created() {
    window.vueApp = this;
    this.v.id = this.$route.query.id;
    this.v.type = this.$route.query.tp;
    this.reloadDetail();

    // 如果是论坛、solution 刷新评论信息
    if (this.v.type == "blog" || this.v.type == "solution") {
      this.reloadComments();
    }
  },
  data() {
    return {
      // blog对应的实体对象
      blogObj: {
        title: "",
        reward: 0,
        content: "",
        zanCount: 0,
        createTime: "",
        creator: ""
      },

      // 详情实体类
      detailObj: {
        beginTime: "",
        company: "",
        content: "",
        createHuman: "",
        createTime: "",
        disableReason: "",
        endTime: "",
        id: "",
        informationFrom: "",
        isDisabled: "",
        location: "",
        salaryMonthMax: "",
        salaryMonthMin: "",
        title: ""
      },

      rule: {},
      show: {
        addComment: false,
        // 是否能上传
        upload: !1,
        readComment: false
      },
      v: {
        // 评论类型：1-吐槽，2-征集，3悬赏
        commentType: 1,
        // 焦点对象
        ed: {},
        //  topsolution id
        id: "",

        type: "",

        // detail
        detail: "",

        // 分页相关
        pageIndex: 1,
        pageSize: 10,
        count: 0,

        // 添加评论的内容
        addCommentContent: "",

        // 添加评论内容的标题
        addCommentTitle: ""
      },
      list: {
        comments: []
      }
    };
  },
  methods: {
    // 刷新 solution 列表
    reloadSolutionDetail() {
      let that = this;
      getSolution({
        id: that.v.id
      }).then(r => {
        if (r.code == 200) {
          that.blogObj = r.data;
        }
      });
    },
    // 添加评论内容清空
    addCommentReset() {
      this.v.addCommentTitle = "";
      this.v.addCommentContent = "";
      this.v.fileCode = "";
    },
    // 评论校验
    valComment() {
      // 如果是快速吐槽
      if (this.v.commentType == 1) {
        if (this.v.addCommentContent == "") {
          this.$message({
            message: "吐槽内容不能为空哦",
            type: "error"
          });
          return false;
        }

        if (this.v.addCommentContent.length > 100) {
          this.$message({
            message: "吐槽炒锅100会挨揍",
            type: "error"
          });
          return false;
        }
      }

      if (this.v.commentType == 2 || this.v.commentType == 3) {
        if (this.v.addCommentTitle == "") {
          this.$message({
            message: "标题不能为空哦",
            type: "error"
          });
          return false;
        }
        if (this.v.addCommentTitle.length > 100) {
          this.$message({
            message: "标题炒锅100也会挨揍",
            type: "error"
          });
          return false;
        }
        if (this.v.addCommentContent == "") {
          this.$message({
            message: "内容不能为空哦",
            type: "error"
          });
          return false;
        }
        if (this.v.addCommentContent.length > 1000000) {
          this.$message({
            message: "内容太持久，少些可以吗",
            type: "error"
          });
          return false;
        }
      }

      return true;
    },
    // 文档上传
    fileUploadSuccess(r) {
      if (r.code == 200) this.v.fileCode = r.fileCode;
    },
    changePange(p) {
      this.v.pageIndex = p;
      this.reloadComments();
    },

    // 添加评论
    addComment(v) {
      // 如果是关闭
      if (v == "cancel") {
        this.v.addCommentContent = "";
        this.v.title = "";
        this.v.fileCode = "";
        this.v.addCommentTitle = "";
        this.show.addComment = false;
      }

      // 如果是显示添加弹窗
      if (v == "show" || v == "want") {
        this.show.addComment = true;
      }

      // 如果是回复悬赏，提供文件上传按钮
      if (v == "want") {
        this.show.upload = !0;
      }
      // 如果是提交
      if (v == "commit") {
        // 内容校验
        if (!this.valComment()) {
          return false;
        }

        // blog添加评论
        if (this.v.type == "blog" || this.v.type == "solution") {
          // 评论内容编码
          this.v.addCommentContent = encodeURIComponent(
            this.v.addCommentContent
          );
          addComment({
            targetId: this.v.id,
            content: this.v.addCommentContent,
            attachmentFileCode: this.v.fileCode
          })
            .then(r => {
              if (r.code == 200 && r.data) {
                this.$message({
                  message: "吐槽成功",
                  type: "success"
                });

                this.show.addComment = false;
                this.reloadComments();
              }
            })
            .catch(e => {});
        }
      }
    },
    // 刷新详情
    reloadDetail() {
      // 如果是招聘信息
      if (this.v.type == "recruit") {
        this.reloadRecruitDetail();
      }

      // 如果是blog
      if (this.v.type == "blog") {
        this.reloadBlogDetail();
      }

      // 如果是 solution
      if (this.v.type == "solution") {
        this.reloadSolutionDetail();
      }
    },
    // 刷新blog信息
    reloadBlogDetail() {
      getBlog({ blogId: this.v.id })
        .then(r => {
          if (r.code == 200) {
            this.v.detail = decodeURIComponent(r.data.content);
            this.blogObj = r.data;
          }
        })
        .catch();
    },
    // 刷新招聘信息
    reloadRecruitDetail() {
      r_detail({ id: this.v.id })
        .then(r => {
          if (r.code == 200) {


            this.detailObj.beginTime = new Date(r.data.beginTime);
            this.detailObj.company = r.data.company;
            this.detailObj.content = decodeURIComponent(r.data.content);
            this.detailObj.createHuman = r.data.createHuman;
            this.detailObj.createTime = new Date(r.data.createTime);
            this.detailObj.disableReason = r.data.disableReason;
            this.detailObj.endTime = new Date(r.data.endTime);
            this.detailObj.informationFrom = r.data.informationFrom;
            this.detailObj.isDisabled = r.data.isDisabled;

            this.detailObj.location = r.data.location;
            this.detailObj.salaryMonthMax = r.data.salaryMonthMax;
            this.detailObj.salaryMonthMin = r.data.salaryMonthMin;
            this.detailObj.title = r.data.title;
          }
        })
        .catch();
    },

    // 刷新评论
    reloadComments() {
      if (this.v.type == "blog" || this.v.type == "solution") {
        this.reloadBlogComments();
      }
    },
    // 刷新blog评论
    reloadBlogComments() {
      findCommentsByTarget({
        targetId: this.v.id,
        pageIndex: this.v.pageIndex,
        pageSize: this.v.pageSize
      })
        .then(r => {
          if (r.code == 200) {
            this.list.comments = r.data || [];
            this.v.count = r.data.count;
            this.list.comments.forEach(e => {
              e.content = decodeURIComponent(e.content);
            });
          }
        })
        .catch(e => {});
    }
  }
};
</script>
<style scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
</style>


