import request from '@/utils/request'
export function add(para) {
  return request({
    url: '/recruit/add',
    method: 'get',
    params: para
  })
}

export function get(para) {
  return request({
    url: '/recruit/get',
    method: 'get',
    params: para
  })
}


export function update(para) {
  return request({
    url: '/recruit/update',
    method: 'get',
    params: para
  })
}


export function del(para) {
  return request({
    url: '/recruit/del',
    method: 'get',
    params: para
  })
}

export function detail(para) {
  return request({
    url: '/recruit/detail',
    method: 'get',
    params: para
  })
}




